@import "../_store-variables.less";
@import "../_store-mixins.less";

@media screen and (min-width:800px){
	.error:before{
		padding-top: 18rem!important;
	}
}

.fn-@{store-ns}-error{
	background: #eee;

	.error{
		&:before{
			padding-top: 65.6%;
			content: "";
			background: data-uri("@{store-image-path}/store-error.png") bottom center no-repeat;
			background-size: 100% auto;
			display: block;
		}

		>p{
			padding: 1rem 1.44rem;
			text-align: center;
			font-size: @font-size-lg;
			color: #666;
		}

		>a{
			.size(94.8%,1.44rem);
			margin: 0 0 0 2.6%;
			text-align: center;
			line-height: 1.44rem;
			font-size: @font-size-lg;
			display: block;
			background: #c20053;
			color: #fff;
			border-radius: 0.2rem;
		}

		&:after{
			padding-bottom: 18.8%;
			content: "";
			display: block;

		}
	}


}
